import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
// nodejs library that concatenates classes
import classNames from "classnames";

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

import ImageIcon from '@material-ui/icons/Image';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  title: {
    flexGrow: 1,
  },
}));


function PartSection() {
  const classes = useStyles();
  
  return (
    <div className={classes.root}>
      
      <h3>Object Part Segmentation</h3>
      <p>In this task, we evaluate the performance of object part segmentation using ScanObjectNN. Objects from real-world scans are often cluttered with background and/or are partial due to occlusions. An approach to better understand such objects are part segmentation. The goal is to segment the input into semantic parts, e.g., a chair into chair back, seat, arm, leg. All other points not belonging to the chair are grouped into a special part named <code>background</code>.
      </p>
      
      <p><b>Data</b>: <i>coming soon</i></p>
      
      <p><b>Metrics</b>: <i>coming soon</i></p>
      
      <p><b>Submission Format</b>: <i>coming soon</i></p>
      
    </div>    
  );
}

export default PartSection;
