import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

import ScanObjectNNTableHardest from './ScanObjectNNTableHardest.js'
import ScanObjectNNTableObjBg from './ScanObjectNNTableObjBg.js'
import ScanObjectNNTableObjOnly from './ScanObjectNNTableObjOnly.js'

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      <Box>{children}</Box>
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function ScanObjectNNTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>

        <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
          <Tab label="Hardest" {...a11yProps(0)} />
          <Tab label="OBJ_BG" {...a11yProps(1)} />
          <Tab label="OBJ_ONLY" {...a11yProps(2)} />
        </Tabs>

      <TabPanel value={value} index={0}>
        <ScanObjectNNTableHardest />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <ScanObjectNNTableObjBg />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <ScanObjectNNTableObjOnly />
      </TabPanel>
    </div>
  );
}