import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
// nodejs library that concatenates classes
import classNames from "classnames";

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

import ImageIcon from '@material-ui/icons/Image';

import ScanObjectNNTabs from './ScanObjectNNTabs.js'

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  title: {
    flexGrow: 1,
  },
}));


function ScanObjectNNSection() {
  const classes = useStyles();
  
  return (
    <div className={classes.root}>
      
      <h3>Object Classification</h3>
      <p>In this task, we evaluate the performance of object classification using ScanObjectNN, a recent real-world point cloud object dataset based on scanned indoor scene data. This dataset poses great challenges because objects from real-world scans are often cluttered with background and/or are partial due to occlusions. 
      </p>
      
      <p><b>Data</b>: The ScanObjectNN dataset can be found <a href="https://github.com/hkust-vgd/scanobjectnn">here</a>. This benchmark reports the results of three variants:
      <ul>
        <li><code>OBJ_ONLY</code>: The simplest variant. The point clouds only contain data on the objects themselves. No background points are included. This is the most similar to classifying CAD models.</li>
        <li><code>OBJ_BG</code>: The medium hard variant. Background points are included in the data.</li>
        <li><code>HARDEST</code>: The most challenging variant. It is similar to <code>OBJ_BG</code> but objects are augmented with translations and rotations.</li>
      </ul>
      You are free to participate and submit the results of any variant. 
      </p>
      
      <p><b>Metrics</b>: We rank all methods according to the overall accuracy, mean per-class accuracy, and per-class accuracy.</p>
      
      <p><b>Submission Format</b>: One single <code>.txt</code> file named after the variant you use. The file should list all object predictions in the test set. Each row is a <code>[object ID] [label index]</code> pair.</p>
      
      <ScanObjectNNTabs />
    </div>    
  );
}

export default ScanObjectNNSection;
