            <p className={classes.pProfile}> <img alt="" className={classes.imgProfile} src={require("assets/img/organizers/thanh.jpg")} /><Link href="https://ducthanhnguyen.weebly.com/">Duc Thanh Nguyen</Link>, Deakin University</p>
