import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
// nodejs library that concatenates classes
import classNames from "classnames";

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

import InstanceTable from './InstanceTable.js'

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  title: {
    flexGrow: 1,
  },
}));


function InstanceSection() {
  const classes = useStyles();
  
  return (
    <div className={classes.root}>
      
      <h3>Instance Segmentation</h3>
      <p>In this task, we evaluate the performance of instance-based semantic segmentation using SceneNN, a real-world scene meshes dataset obtained from indoor scene scanning. We follow the NYU-D v2 dataset to define 40 semantic classes. 
      </p>
      
      <p><b>Data</b>: <i>coming soon</i></p>
      
      <p><b>Metrics</b>: We rank all methods according to mean average precision (mAP).</p>
      
      <p><b>Submission Format</b>: <i>coming soon</i></p>
      
      <InstanceTable />
    </div>    
  );
}

export default InstanceSection;
